import { Component, OnInit } from '@angular/core';
import { Task } from 'src/app/models/task';

@Component({
    selector: 'app-home-tasks',
    templateUrl: './home-tasks.component.html',
    styleUrls: ['./home-tasks.component.css']
})
export class HomeTasksComponent implements OnInit {
    public tasks: Task[];
    public loading: boolean = true;
    constructor() { }
    public tasksFromLocal = [];

    ngOnInit(): void {

        let homeTasks = localStorage.getItem("tasks");
        if (homeTasks) {
            homeTasks = JSON.parse(homeTasks)
            for (let i = 0; i < homeTasks.length; i++) {
                this.tasksFromLocal.push(homeTasks[i])
            }
            this.tasks = this.tasksFromLocal;
        }


    }

    deleteTask(_id): void {

        let homeTasks = this.tasks;
        let taskaArr = [];

        if (homeTasks) {

            for (let i = 0; i < homeTasks.length; i++) {

                if (homeTasks[i]._id === _id) {

                    homeTasks.splice(i,1);
                    console.log(homeTasks)
                    for(let i = 0; i < homeTasks.length; i++ ){

                        taskaArr.push(homeTasks[i])

                    }

                    localStorage.setItem("tasks", JSON.stringify(taskaArr));

                }

            }

        }

    }


}
